import Vue from 'vue'
import Vuex from 'vuex'

import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    menuIndex: '1',
    menuPath: '',
    articleId: '',
    authorId: '',
    token: '',
    headImage: '',
    userName: ''
  },
  getters: {
  },
  mutations: {
    menuIndex(state, param) {
      state.menuIndex = param
    },
    menuPath(state, param) {
      state.menuPath = param
    },
    articleId(state, param) {
      state.articleId = param
    },
    authorId(state, param) {
      state.authorId = param
    },
    token(state, param) {
      state.token = param
    },
    headImage(state, param) {
      state.headImage = param
    },
    userName(state, param) {
      state.userName = param
    }
  },
  actions: {
  },
  modules: {
  },
  plugins: [createPersistedState()]
})
