<template>
  <div id="gallerManager">
    <div style="height: 40px;line-height: 36px;text-align: right;margin-right: 2px;">
      <el-button size="small" type="primary" @click="upload">上传</el-button>
    </div>
    <el-collapse v-model="activeNames" accordion>
      <el-collapse-item title="图片列表" name="1">
        <el-table :data="tableData" border style="width: 100%;font-size: 10px;">
          <el-table-column prop="id" label="编号" min-width="30" sortable>
          </el-table-column>
          <el-table-column prop="url" label="地址" min-width="40">
          </el-table-column>
          <el-table-column prop="status" label="状态" min-width="10">
            <template slot-scope="scope">
              <el-switch v-model="scope.row.status" @change="change(scope.row)" active-value="已启用" inactive-value="已禁用"
                active-color="#13ce66" inactive-color="#ff4949">
              </el-switch>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="20">
            <template slot-scope="scope">
              <el-button @click="detail(scope.row)" type="primary" size="mini" circle><i class="el-icon-view"></i>
              </el-button>
              <el-button @click="dele(scope.row)" type="danger" icon="el-icon-delete" size="mini" circle></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination :page-sizes="[5, 10, 15, 20]" layout="total, sizes, prev, pager, next, jumper"
          :page-size.sync="queryForm.pageSize" :total="pageInfo.total" :current-page.sync="queryForm.pageNum"
          @size-change="query" @current-change="query">
        </el-pagination>
      </el-collapse-item>
    </el-collapse>

    <el-dialog :title="title" :visible.sync="show" width="400px" @close="clearForm" center>
      <img v-if="title == '预览'" :src="imageUrl" width="360px" />
      <div id="uploader">
        <el-upload v-if="title == '上传'" class="avatar-uploader" action="" :http-request="elUploadImage"
          :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload" :show-file-list="false">
          <img v-if="form.url" :src="'/api/resources/' + form.url" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button v-if="title === '上传'" type="primary" @click="submitForm">提交</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'GalleryManager',
  data() {
    return {
      activeNames: ['1'],
      tableData: [],
      queryForm: {
        pageNum: 1,
        pageSize: 5
      },
      pageInfo: {
        total: 0
      },
      show: false,
      title: '预览',
      imageUrl: '',
      form: {}
    }
  },
  created() {
    this.query()
  },
  methods: {
    clearForm() {
      this.form = {}
    },
    submitForm() {
      this.$axios({
        method: 'post',
        url: 'api/gallery/insert',
        data: this.form
      }).then(res => {
        this.$message({
          type: 'success',
          message: res.data.msg
        })
        this.query()
      }).catch(err => {
        console.info(err)
      })
    },
    change(row) {
      this.$axios({
        method: 'post',
        url: 'api/gallery/update',
        data: row
      }).then(res => {
        this.$message({
          type: 'success',
          message: res.data.msg
        })
        this.query()
      }).catch(err => {
        console.info(err)
      })
    },
    dele(row) {
      this.$axios({
        method: 'post',
        url: 'api/gallery/delete',
        data: row
      }).then(res => {
        this.$message({
          type: 'success',
          message: res.data.msg
        })
        this.query()
      }).catch(err => {
        console.info(err)
      })
    },
    detail(row) {
      this.title = '预览'
      this.show = true
      this.imageUrl = '/api/resources/' + row.url
    },
    upload() {
      this.title = '上传'
      this.show = true
    },
    query() {
      this.$axios({
        method: 'post',
        url: 'api/gallery/list',
        data: this.queryForm
      }).then(res => {
        this.tableData = res.data.obj.list
        this.pageInfo.total = res.data.obj.total
      }).catch(err => {
        console.info(err)
      })
    },
    handleAvatarSuccess(res, file) {
      this.form.image = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!')
      }
      return isLt2M
    },
    elUploadImage($file) {
      const formdata = new FormData()
      formdata.append('image', $file.file)
      this.$axios({
        method: 'post',
        url: 'api/article/uploadImage',
        data: formdata,
        headers: { 'Content-Type': 'multipart/form-data' }
      }).then(res => {
        this.form.url = res.data.obj
        this.$message({
          type: 'success',
          message: '上传完成'
        })
        this.$forceUpdate()
      }).catch(err => {
        console.info(err)
      })
    }
  }
}
</script>

<style>
#gallerManager {
  height: 100%;
  background-color: #fff;
}

.el-collapse {
  text-align: center;
}

#uploader {
  width: 100%;
  text-align: center;
}

#uploader .avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  width: 150px;
  height: 150px;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

#uploader .avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}

#uploader .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 150px;
  height: 150px;
  line-height: 150px;
  text-align: center;
}

#uploader .avatar {
  height: 150px;
  display: block;
}
</style>
