<template>
  <div id="menu">
    <el-container>
      <el-header style="text-align: left;line-height: 60px;">
        <span v-if="isCollapse" @click="isCollapse = false" style="font-size: 54px;"> <i
            class="el-icon-s-fold"></i></span>
        <span v-else @click="isCollapse = true" style="font-size: 54px;"> <i class="el-icon-s-unfold"></i></span>
        <div id="aside">
          <el-menu v-if="isCollapse" :unique-opened="true" :default-active="menuIndex" class="el-menu-vertical-demo"
            background-color="#5f5f5f" text-color="#fefefe" active-text-color="#ff4ff0" style="height: 100%;">
            <el-menu-item index="1" @click="menu('1', 'home')">
              <template slot="title">
                <i class="el-icon-s-home"></i>
                <span slot="title">主页</span>
              </template>
            </el-menu-item>
            <el-submenu index="2">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span slot="title">创作</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="2-1" @click="menu('2-1', 'write')"> <i class="el-icon-menu"></i> <span>发布文章</span>
                </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span slot="title">管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="3-1" @click="menu('3-1', 'userManager')"> <i class="el-icon-menu"></i>
                  <span>用户管理</span>
                </el-menu-item>
                <el-menu-item index="3-2" @click="menu('3-2', 'articleManager')"> <i class="el-icon-menu"></i>
                  <span>文章管理</span>
                </el-menu-item>
                <el-menu-item index="3-3" @click="menu('3-3', 'galleryManager')"> <i class="el-icon-menu"></i>
                  <span>主页图库</span>
                </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="4">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span slot="title">个人中心</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="4-1" @click="menu('4-1', 'myInfo')"> <i class="el-icon-menu"></i>
                  <span>账号信息</span>
                </el-menu-item>
                <el-menu-item index="4-2" @click="menu('4-2', 'myArticle')"> <i class="el-icon-menu"></i>
                  <span>文章管理</span>
                </el-menu-item>
                <el-menu-item index="4-3" @click="menu('4-3', 'myCollect')"> <i class="el-icon-menu"></i>
                  <span>我的收藏</span>
                </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </div>

        <div v-if="isLogin" style="position: absolute; right:5px;top:0;">
          <el-dropdown @visible-change="getInfo">
            <span class="el-dropdown-link">
              <el-avatar v-if="headImage" :size="54" :src="headImage"></el-avatar>
              <el-avatar v-else :size="54" icon="el-icon-user-solid"></el-avatar>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item disabled>账号：{{ info.id }}</el-dropdown-item>
              <el-dropdown-item disabled>昵称：{{ info.name }}</el-dropdown-item>
              <el-dropdown-item disabled>等级：{{ info.level }}</el-dropdown-item>
              <el-divider></el-divider>
              <el-dropdown-item @click.native="menu('4-1', 'myInfo')">修改资料</el-dropdown-item>
              <el-dropdown-item @click.native="logout">退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <div v-else style="position: absolute; right:5px;top:0;">
          <el-avatar v-if="headImage" @click.native="openDialog('登录')" :size="54" :src="headImage"></el-avatar>
          <el-avatar v-else @click.native="openDialog('登录')" :size="54" icon="el-icon-user-solid"></el-avatar>
        </div>

      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
      <el-footer>

      </el-footer>
    </el-container>

    <el-dialog :title="title" :visible.sync="show" width="400px" center>
      <el-input type="hidden" :name="dialogType"></el-input>
      <el-form :model="form" :rules="rules" ref="form" label-width="80px">
        <el-form-item v-if="title == '登录'" label="账号" prop="id">
          <el-input v-model="form.id" clearable :disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item v-if="title == '注册'" label="电话" prop="phone">
          <el-input v-model="form.phone" clearable :disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item v-if="title == '注册'" label="昵称" prop="name">
          <el-input v-model="form.name" clearable :disabled="disabled" maxlength="25" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="form.password" clearable :disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item v-if="title == '注册'" label="确认密码" prop="repassword">
          <el-input v-model="form.repassword" clearable :disabled="disabled"></el-input>
        </el-form-item>
      </el-form>
      <div v-if="title == '注册'" style="text-align:right;cursor:default;">
        <span>有账号?去</span><span style="color:blue;cursor: pointer;" @click="openDialog('登录')">登录</span>
      </div>
      <div v-else style="text-align:right;cursor: default;">
        <span>没有账号?去</span><span style="color:blue;cursor: pointer;" @click="openDialog('注册')">注册</span>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">提交</el-button>
        <el-button @click="closeDialog">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Menu',
  data() {
    const validatePass = (rule, value, callback) => {
      if (this.form.password !== '' && value === undefined) {
        callback(new Error('请再次输入密码'))
      } else if (this.form.password !== '' && value !== this.form.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      show: false,
      title: '登录',
      dialogType: '',
      disabled: false,
      isCollapse: false,
      form: {},
      info: {},
      rules: {
        id: [{
          required: true,
          message: '请输入账号',
          trigger: 'blur'
        }],
        phone: [{
          required: true,
          message: '请输入电话号码',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: '请输入密码',
          trigger: 'blur'
        }],
        repassword: [{
          required: true,
          validator: validatePass,
          trigger: 'blur'
        }]
      }
    }
  },
  computed: {
    headImage() {
      return this.$store.state.headImage
    },
    userName() {
      return this.$store.state.userName
    },
    menuIndex() {
      return this.$store.state.menuIndex
    },
    isLogin() {
      if (this.$store.state.token !== '') return true
      else return false
    }
  },
  created() {
  },
  methods: {
    menu(index, name) {
      this.$store.commit('menuIndex', index)
      this.$store.commit('menuPath', name)
      this.$router.push(name).catch(err => { const str = err; err = str })
    },
    getInfo($event) {
      if (this.$store.state.token === '') {
        return false
      } else if ($event) {
        this.$axios({
          method: 'post',
          url: 'api/user/myInfo',
          data: {
            pageSize: 1,
            pageNum: 1
          }
        }).then(res => {
          this.info = res.data.obj
        }).catch(err => {
          console.info(err)
        })
      } else return false
    },
    openDialog(val) {
      this.form = {}
      this.show = true
      this.title = val
      this.$nextTick(() => {
        this.$refs.form.clearValidate()
      })
    },
    closeDialog() {
      this.form = {}
      this.show = false
    },
    submitForm() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.title === '注册') {
            this.$axios({
              method: 'post',
              url: 'api/user/insert',
              data: this.form
            }).then(res => {
              if (res.data.success === true) {
                const data = res.data.obj
                this.$store.commit('token', res.data.msg)
                if (data.headImage !== undefined) this.$store.commit('headImage', '/api/resources/' + data.headImage)
                else this.$store.commit('headImage', '')
                this.$store.commit('userName', data.userName)
                this.$message({
                  message: '注册并登陆成功！',
                  type: 'success'
                })
                this.$forceUpdate()
              } else {
                this.$store.commit('token', '')
                this.$store.commit('headImage', '')
                this.$message.error(res.data.msg)
              }
            }).catch(err => {
              console.info(err)
            })
            setTimeout(() => { this.show = false }, 300)
          } else if (this.title === '登录') {
            this.$axios({
              method: 'post',
              url: 'api/user/login',
              data: this.form
            }).then(res => {
              if (res.data.success === true) {
                const data = res.data.obj
                this.$store.commit('token', res.data.msg)
                if (data.headImage !== undefined) this.$store.commit('headImage', '/api/resources/' + data.headImage)
                else this.$store.commit('headImage', '')
                this.$store.commit('userName', data.userName)
                this.$message({
                  message: '登陆成功！',
                  type: 'success'
                })
                this.$forceUpdate()
              } else {
                this.$store.commit('token', '')
                this.$store.commit('headImage', '')
                this.$message.error(res.data.msg)
              }
            }).catch(err => {
              this.$message.error(err)
            })
            setTimeout(() => { this.show = false }, 300)
          }
        } else {
          return false
        }
      })
    },
    logout() {
      this.$axios({
        method: 'post',
        url: 'api/user/logout',
        data: {}
      }).then(res => {
        this.$store.commit('token', '')
        this.$store.commit('headImage', '')
        this.$store.commit('userName', '')
        this.$message({
          message: res.data.msg,
          type: 'success'
        })
      }).catch(err => {
        console.info(err)
      })
    }
  }
}
</script>

<style>
#aside {
  position: absolute;
  top: 60px;
  left: 0px;
  height: 100%;
  z-index: 10;
}

#menu .el-header {
  position: fixed;
  height: 100px;
  line-height: 100px;
  width: 100%;
  z-index: 10;
}

.el-menu {
  height: 100%;
}

.el-dropdown {
  width: 54px;
  height: 54px;
}

.el-dropdown-menu {
  width: 200px;
  height: 300px;
  color: #808080;
}

.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}

.el-header,
.el-footer {
  background-color: #B3C0D1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #D3DCE6;
  color: #333;
  text-align: center;
  line-height: 200px;
}

.el-main {
  position: relative;
  top: 50px;
  background-color: #E9EEF3;
  color: #333;
}

body>.el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>
