<template>
  <div id="articleManager">
    <el-collapse v-model="activeNames" accordion>
      <el-collapse-item title="文章列表" name="1">
        <el-table :data="tableData" border style="width: 100%;font-size: 10px;">
          <el-table-column prop="id" label="编号" min-width="20" sortable>
          </el-table-column>
          <el-table-column prop="title" label="标题" min-width="20">
          </el-table-column>
          <el-table-column prop="collection" label="收藏数" min-width="10" sortable>
          </el-table-column>
          <el-table-column prop="views" label="阅览量" min-width="10" sortable>
          </el-table-column>
          <el-table-column prop="authorId" label="作者" min-width="10">
          </el-table-column>
          <el-table-column prop="uploadDate" label="日期" min-width="10" sortable>
          </el-table-column>
          <el-table-column prop="status" label="状态" min-width="10">
          </el-table-column>
          <el-table-column label="操作" min-width="20">
            <template slot-scope="scope">
              <el-button @click="detail(scope.row)" type="primary" size="mini" circle><i class="el-icon-view"></i>
              </el-button>
              <el-button @click="edit(scope.row)" type="primary" size="mini" circle><i class="el-icon-edit"></i>
              </el-button>
              <el-button @click="dele(scope.row)" type="danger" icon="el-icon-delete" size="mini" circle></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination :page-sizes="[5, 10, 15, 20]" layout="total, sizes, prev, pager, next, jumper"
          :page-size.sync="queryForm.pageSize" :total="pageInfo.total" :current-page.sync="queryForm.pageNum"
          @size-change="query" @current-change="query">
        </el-pagination>
      </el-collapse-item>
    </el-collapse>

    <el-dialog :title="title" @close="query" :visible.sync="show" width="80%" center>
      <!--这里的v-if的作用是让组件动态创建，解决了props传参无法和父组件同步问题-->
      <Write v-if="show" :p_mode="mode" :p_authorId="form.authorId" :p_id="form.id" :p_status="form.status"
        :p_title="form.title" :p_text="form.text" :p_content="form.content" :p_image="form.image" :p_html="form.html"
        :p_collection="form.collection" :p_views="form.views">
      </Write>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>

import Write from '../../article/Write.vue'

export default {
  name: 'MyArticle',
  data() {
    return {
      activeNames: ['1'],
      tableData: [],
      queryForm: {
        pageNum: 1,
        pageSize: 5
      },
      pageInfo: {
        total: 0
      },
      title: '',
      show: false,
      form: {},
      mode: 'readonly'
    }
  },
  created() {
    this.query()
  },
  methods: {
    detail(row) {
      this.title = '预览'
      this.mode = 'readonly'
      this.form = Object.assign({}, row)
      this.show = true
    },
    edit(row) {
      this.title = '编辑'
      this.mode = 'update'
      this.form = Object.assign({}, row)
      this.show = true
    },
    dele(row) {
      this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios({
          method: 'post',
          url: 'api/article/deleteArticle',
          data: row
        }).then(res => {
          this.$message({
            type: 'success',
            message: res.data.msg
          })
          this.query()
        }).catch(err => {
          console.info(err)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    closeDialog() {
      this.show = false
    },
    query() {
      this.$axios({
        method: 'post',
        url: 'api/article/mylist',
        data: this.queryForm
      }).then(res => {
        this.tableData = res.data.obj.list
        this.pageInfo.total = res.data.obj.total
      }).catch(err => {
        console.info(err)
      })
    }
  },
  components: {
    Write
  }
}
</script>

<style>
#articleManager {
  height: 100%;
  background-color: #fff;
}

.el-collapse {
  text-align: center;
}
</style>
