<template>
  <div id="write">
    <div id="write_title">
      <el-form ref="form" :model="form" :rules="rules" label-width="100px">
        <el-form-item label="标题" prop="title">
          <el-col>
            <el-input type="textarea" maxlength="50" show-word-limit :autosize="{ minRows: 2, maxRows: 3 }"
              v-model="form.title" :readonly="readonly" placeholder="仅预览时使用"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="简介" prop="text">
          <el-col>
            <el-input type="textarea" maxlength="200" :autosize="{ minRows: 2, maxRows: 10 }" show-word-limit
              v-model="form.text" :value='form.text' :readonly="readonly" placeholder="仅预览时使用"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-col>
            <el-select v-model="form.status" placeholder="请选择" :disabled="readonly == true">
              <el-option v-for="item in articleStatus" :key="item" :label="item" :value="item">
              </el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="封面">
          <el-col id="upload-image">
            <el-upload :style="{ 'pointer-events': componentStatus }" class="avatar-uploader" action=""
              :http-request="elUploadImage" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload"
              :show-file-list="false">
              <img v-if="form.image" :src="'/api/resources/' + form.image" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-col>
        </el-form-item>
      </el-form>
    </div>
    <div id="write_content">
      <mavon-editor :style="{ 'pointer-events': componentStatus }" :boxShadow="false" toolbarsBackground="#eeeeee"
        ref="md" v-model="form.content" @imgAdd="uploadImage" @save="uploadFile" />
    </div>
    <div id="write_foot"></div>
  </div>
</template>

<script>

export default {
  name: 'Write',
  props: ['p_mode', 'p_authorId', 'p_id', 'p_status', 'p_title', 'p_text', 'p_content', 'p_image', 'p_html', 'p_collection', 'p_views'],
  data() {
    return {
      readonly: false,
      componentStatus: 'auto',
      form: {
        authorId: this.p_authorId,
        id: this.p_id,
        status: this.p_status,
        title: this.p_title,
        text: this.p_text,
        content: this.p_content,
        image: this.p_image,
        html: this.p_html,
        collection: this.p_collection,
        views: this.p_views
      },
      articleStatus: ['暂存', '发布'],
      rules: {
        title: [{
          required: true,
          message: '请输入标题',
          trigger: 'blur'
        }],
        status: [{
          required: true,
          message: '请选择状态',
          trigger: 'change'
        }]
      }
    }
  },
  created() {
    if (this.p_mode === 'readonly') {
      this.readonly = true
      this.componentStatus = 'none'
    }
  },
  methods: {
    show() {
      console.info(this.form)
    },
    handleAvatarSuccess(res, file) {
      this.form.image = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!')
      }
      return isLt2M
    },
    elUploadImage($file) {
      const formdata = new FormData()
      formdata.append('image', $file.file)
      this.$axios({
        method: 'post',
        url: 'api/article/uploadImage',
        data: formdata,
        headers: { 'Content-Type': 'multipart/form-data' }
      }).then(res => {
        console.info(res)
        this.form.image = res.data.obj
      }).catch(err => {
        console.info(err)
      })
    },
    uploadImage(pos, $file) {
      const formdata = new FormData()
      formdata.append('image', $file)
      this.$axios({
        method: 'post',
        url: 'api/article/uploadImage',
        data: formdata,
        headers: { 'Content-Type': 'multipart/form-data' }
      }).then(res => {
        this.$refs.md.$img2Url(pos, '/api/resources/' + res.data.obj)
      }).catch(err => {
        console.info(err)
      })
    },
    uploadFile($file, render) {
      let ajaxUrl = 'api/article/uploadArticle'
      if (this.p_mode === 'update') {
        ajaxUrl = 'api/article/updateArticle'
      }
      this.form.html = render
      this.$confirm('是否确认保存？', '确认信息', {
        distinguishCancelAndClose: true,
        confirmButtonText: '保存',
        cancelButtonText: '取消'
      })
        .then(() => {
          this.$refs.form.validate((valid) => {
            if (this.form.content === undefined || this.form.content === '') {
              this.$message.error('内容不能为空')
              return false
            }
            if (valid) {
              this.$axios({
                method: 'post',
                url: ajaxUrl,
                data: this.form
              }).then(res => {
                this.$message({
                  type: 'success',
                  message: '上传成功'
                })
              }).catch(err => {
                console.info(err)
              })
            } else {
              return false
            }
          })
        })
        .catch(action => {
          this.$message({
            type: 'info',
            message: action === 'cancel'
              ? '放弃保存并离开页面'
              : '停留在当前页面'
          })
        })
    }
  }
}
</script>

<style>
#write {
  height: 100;
  padding: 4px;
  margin-bottom: 2px;
  box-shadow: 0 0 2px #aaaaaa;
  background-color: #ffffff;
}

#write_title {
  background-color: #ffffff;
}

#write_title .el-col {
  width: 50%;
}

#upload-image .avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

#upload-image .avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}

#upload-image .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
}

#upload-image .avatar {
  width: 100px;
  height: 100px;
  display: block;
}
</style>
