<template>
  <div id="card">
    <el-empty v-if="isEmpty" description="文章已被删除！" @click.native="desCollect" :image-size="100">
    </el-empty>
    <div v-else :style="{ background: 'url(' + image + ')', backgroundSize: 'auto 100%' }">
      <div id="card_head" @click="display">
        <h5>{{ title }}</h5>
      </div>
      <div id="card_main" @click="display">
        <div style="width: 100%;font-size:8px;">
          <p>{{ text }}</p>
        </div>
      </div>
      <div id="card_foot">
        <span><i class="el-icon-collection-tag">{{ collection }}</i></span>
        <span><i class="el-icon-view">{{ views }}</i></span>
        <span>{{ uploadDate }}</span>
        <span @click="authorInfo">{{ authorName }}</span>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Card',
  props: ['p_id', 'p_title', 'p_text', 'p_authorName', 'p_authorId',
    'p_uploadDate', 'p_content', 'p_image', 'p_status', 'p_collection', 'p_views'],
  data() {
    return {
      id: this.p_id,
      title: this.p_title,
      text: this.p_text,
      image: this.p_image === undefined ? '' : this.p_image,
      uploadDate: this.p_uploadDate,
      content: this.p_content,
      authorName: this.p_authorName,
      authorId: this.p_authorId,
      status: this.p_status,
      collection: this.p_collection,
      views: this.p_views
    }
  },
  computed: {
    isEmpty() {
      if (this.uploadDate === undefined) return true
      else return false
    }
  },
  methods: {
    authorInfo() {
      this.$store.commit('authorId', this.authorId)
      this.$router.push('authorInfo')
    },
    display() {
      this.$store.commit('articleId', this.id)
      this.$router.push('Article')
    },
    desCollect() {
      this.$confirm('取消收藏该文章, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios({
          method: 'post',
          url: 'api/collection/desCollect',
          data: {
            articleId: this.id
          }
        }).then(res => {
          this.$message({
            type: 'success',
            message: res.data.msg
          })
        }).catch(err => {
          console.info(err)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消操作'
        })
      })
    }
  }
}
</script>

<style>
#card {
  margin: 10px 10px 0 10px;
  background-color: rgb(239, 239, 239);
  box-shadow: 0 0 3px #333333;
  height: 200px;
  width: 320px;
  display: inline-block;
  overflow: hidden;
  padding: 4px;
  cursor: pointer;
}

#card_head {
  background-color: #eeeeee;
  text-align: left;
  height: 22px;
  color: #555555;
}

#card_head h5 {

  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

#card_main {
  transition-duration: 1s;
  transition-timing-function: linear;
  opacity: 0;
  height: 150px;
}

#card_main:hover {
  transition-duration: 0.6s;
  transition-timing-function: linear;
  background-color: #eeeeee;
  opacity: 0.7;
}

#card_main img {
  height: 148px;
  box-shadow: 0 0 20x #eeeeee;
  border-radius: 2px;
}

#card_main div {
  height: 150px;
  float: left;
  overflow: hidden;
}

#main p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 8;
  -webkit-box-orient: vertical;
  color: #555555;
}

#card_foot {
  background-color: #eeeeee;
  text-align: right;
  line-height: 20px;
  height: 20px;
  font-size: 5px;
  bottom: 1px;
  color: #555555;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 8;
  -webkit-box-orient: vertical;
}

#card_foot span {
  margin-right: 4px;
}
</style>
