<template>
  <div id="article">
    <div id="article_head"></div>
    <div id="article_content">
      <div id="article_message">
        <div v-if="isCollected" style="color:coral;" @click="collect"><i class="el-icon-collection-tag">{{
            form.collection
        }}</i></div>
        <div v-else @click="collect"><i class="el-icon-collection-tag">{{ form.collection }}</i></div>
        <div><i class="el-icon-view">{{ form.views }}</i></div>
        <div>发布时间：{{ form.uploadDate }}</div>
        <div class="author"> 作者：<span @click="authorInfo">{{ form.authorId }}</span></div>
      </div>

      <mavon-editor ref="md" :value="form.content" :toolbarsFlag="false" :subfield="false" defaultOpen="preview"
        :boxShadow="false" />
    </div>
    <div id="article_foot"></div>
  </div>
</template>

<script>

export default {
  name: 'Article',
  data() {
    return {
      form: {},
      isCollected: false
    }
  },
  mounted() {
    this.query()
  },
  created() {
    const timer = setTimeout(() => { this.addViews() }, 10000)
    this.$once('hook:beforeDestroy', () => {
      clearInterval(timer)
    })
  },
  methods: {
    authorInfo() {
      this.$store.commit('authorId', this.form.authorId)
      this.$router.push('authorInfo')
    },
    isExist() {
      this.$axios({
        method: 'post',
        url: 'api/collection/exist',
        data: {
          articleId: this.form.id
        }
      }).then(res => {
        this.isCollected = res.data.obj
      }).catch(err => {
        console.info(err)
      })
    },
    addViews() {
      this.$axios({
        method: 'post',
        url: 'api/article/addViews',
        data: {
          id: this.$store.state.articleId
        }
      }).then(res => {
        this.query()
      }).catch(err => {
        console.info(err)
      })
    },
    collect() {
      this.$axios({
        method: 'post',
        url: 'api/collection/exist',
        data: {
          articleId: this.form.id
        }
      }).then(res => {
        if (res.data.obj === false) {
          this.$axios({
            method: 'post',
            url: 'api/collection/collect',
            data: {
              articleId: this.form.id
            }
          }).then(res => {
            this.$message({
              type: 'success',
              message: res.data.msg
            })
            this.query()
          }).catch(err => {
            console.info(err)
          })
        } else {
          this.$axios({
            method: 'post',
            url: 'api/collection/desCollect',
            data: {
              articleId: this.form.id
            }
          }).then(res => {
            this.$message({
              type: 'success',
              message: res.data.msg
            })
            this.query()
          }).catch(err => {
            console.info(err)
          })
        }
      }).catch(err => {
        console.info(err)
      })
    },
    query() {
      this.$axios({
        method: 'post',
        url: 'api/article/view',
        data: {
          id: this.$store.state.articleId
        }
      }).then(res => {
        this.form = res.data.obj
        if (this.$store.state.token !== '') this.isExist()
      }).catch(err => {
        console.info(err)
      })
    }
  }
}
</script>

<style>
#article {
  height: 100%;
  background-color: #ffffff;
  box-shadow: 0 0 2px #888888;
}

#article_message {
  background-color: #c2c2c2;
  color: #333;
  font-size: 20px;
  text-align: left;
  height: auto;
  line-height: 60px;
  width: 100%;
  cursor: pointer;
}

#article_message div {
  margin-right: 5px;
  display: inline-block;
}

#article_message span {
  margin-right: 10px;
}
</style>
