import Vue from 'vue'
import VueRouter from 'vue-router'
import Menu from '../components/home/Menu.vue'
import Home from '../components/home/Home.vue'

import Article from '../components/article/Article.vue'
import ArticleManager from '../components/article/ArticleManager.vue'
import Write from '../components/article/Write.vue'

import GalleryManager from '../components/gallery/GalleryManager.vue'

import UserManager from '../components/user/UserManager.vue'
import AuthorInfo from '../components/user/AuthorInfo.vue'

import MyArticle from '../components/user/manager/MyArticle.vue'
import MyInfo from '../components/user/manager/MyInfo.vue'
import MyCollect from '../components/user/manager/MyCollect.vue'
Vue.use(VueRouter)
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const routes = [
  {
    path: '/',
    redirect: 'home'
  },
  {
    path: '/menu',
    name: 'Menu',
    component: Menu,
    children: [
      {
        path: '/home',
        name: 'Home',
        component: Home
      },
      {
        path: '/article',
        name: 'Article',
        component: Article
      },
      {
        path: '/articleManager',
        name: 'ArticleManager',
        component: ArticleManager
      },
      {
        path: '/write',
        name: 'Write',
        component: Write
      },
      {
        path: '/galleryManager',
        name: 'GalleryManager',
        component: GalleryManager
      },
      {
        path: '/userManager',
        name: 'UserManager',
        component: UserManager
      },
      {
        path: '/myArticle',
        name: 'MyArticle',
        component: MyArticle
      },
      {
        path: '/myInfo',
        name: 'MyInfo',
        component: MyInfo
      },
      {
        path: '/myCollect',
        name: 'MyCollect',
        component: MyCollect
      },
      {
        path: '/authorInfo',
        name: 'AuthorInfo',
        component: AuthorInfo
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
