<template>
  <div id="userManager">
    <el-collapse v-model="activeNames" accordion>
      <el-collapse-item title="用户列表" name="1">
        <el-table :data="tableData" border style="width: 100%;font-size: 10px;">
          <el-table-column prop="id" label="账号" min-width="15" sortable>
          </el-table-column>
          <el-table-column prop="name" label="昵称" min-width="15">
          </el-table-column>
          <el-table-column prop="registrationDate" label="注册日期" min-width="15" sortable>
          </el-table-column>
          <el-table-column prop="status" label="状态" min-width="10">
          </el-table-column>
          <el-table-column prop="sign" label="签名" min-width="15">
          </el-table-column>
          <el-table-column prop="level" label="等级" min-width="5" sortable>
          </el-table-column>
          <el-table-column prop="experience" label="经验" min-width="10">
          </el-table-column>
          <el-table-column label="操作" min-width="15">
            <template slot-scope="scope">
              <el-button @click="detail(scope.row)" type="primary" circle size="mini"><i class="el-icon-view"></i>
              </el-button>
              <el-button @click="editor(scope.row)" type="primary" circle size="mini"><i class="el-icon-edit"></i>
              </el-button>
              <el-button @click="dele(scope.row)" type="danger" circle size="mini"><i class="el-icon-delete"></i>
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination :page-sizes="[5, 10, 15, 20]" layout="total, sizes, prev, pager, next, jumper"
          :page-size.sync="queryForm.pageSize" :total="pageInfo.total" :current-page.sync="queryForm.pageNum"
          @size-change="query" @current-change="query">
        </el-pagination>
      </el-collapse-item>
    </el-collapse>

    <el-dialog :title="title" :visible.sync="show" width="400px" center>
      <el-input type="hidden" :name="dialogType"></el-input>
      <el-form :model="form" label-width="80px">
        <el-form-item label="头像">
          <div id="head-uploader">
            <el-upload :style="{ 'pointer-events': componentStatus }" class="avatar-uploader" action=""
              :http-request="elUploadImage" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload"
              :show-file-list="false">
              <img v-if="form.headImg" :src="'/api/resources/' + form.headImg" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </div>
        </el-form-item>
        <el-form-item label="账号" prop="id">
          <el-input v-model="form.id" clearable disabled></el-input>
        </el-form-item>
        <el-form-item label="昵称" prop="name">
          <el-input v-model="form.name" clearable :disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="phone">
          <el-input v-model="form.phone" clearable :disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="注册日期" prop="registrationDate">
          <el-input v-model="form.registrationDate" clearable disabled></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-select v-model="form.status" placeholder="请选择" :disabled="disabled">
            <el-option label="已启用" value="已启用"></el-option>
            <el-option label="已禁用" value="已禁用"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="签名" prop="sign">
          <el-input v-model="form.sign" clearable :disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="等级" prop="level">
          <el-input v-model="form.level" clearable :disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="经验" prop="experience">
          <el-input v-model="form.experience" clearable :disabled="disabled"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button v-if="title === '编辑'" type="primary" @click="submitForm">提交</el-button>
        <el-button @click="closeDialog">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'UserManager',
  data() {
    return {
      activeNames: ['1'],
      tableData: [],
      queryForm: {
        pageNum: 1,
        pageSize: 5
      },
      pageInfo: {
        total: 0
      },
      title: '详情',
      dialogType: '',
      show: false,
      form: {},
      disabled: true,
      componentStatus: 'none'

    }
  },
  created() {
    this.query()
  },
  methods: {
    detail(row) {
      this.title = '详情'
      this.form = row
      this.disabled = true
      this.componentStatus = 'none'
      this.show = true
    },
    editor(row) {
      this.title = '编辑'
      this.componentStatus = 'auto'
      this.form = row
      this.disabled = false
      this.show = true
    },
    submitForm() {
      this.$axios({
        method: 'post',
        url: 'api/user/update',
        data: this.form
      }).then(res => {
        this.$message({
          type: 'success',
          message: res.data.msg
        })
        this.show = false
        this.query()
      }).catch(err => {
        console.info(err)
      })
    },
    closeDialog() {
      this.show = false
    },
    query() {
      this.$axios({
        method: 'post',
        url: 'api/user/list',
        data: this.queryForm
      }).then(res => {
        this.tableData = res.data.obj.list
        this.pageInfo.total = res.data.obj.total
      }).catch(err => {
        console.info(err)
      })
    },
    handleAvatarSuccess(res, file) {
      this.form.image = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!')
      }
      return isLt2M
    },
    elUploadImage($file) {
      const formdata = new FormData()
      formdata.append('image', $file.file)
      this.$axios({
        method: 'post',
        url: 'api/article/uploadImage',
        data: formdata,
        headers: { 'Content-Type': 'multipart/form-data' }
      }).then(res => {
        this.form.headImg = res.data.obj
        this.$forceUpdate()
      }).catch(err => {
        console.info(err)
      })
    },
    dele(row) {
      this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios({
          method: 'post',
          url: 'api/user/delete',
          data: row
        }).then(res => {
          this.$message({
            type: 'success',
            message: res.data.msg
          })
          this.query()
        }).catch(err => {
          console.info(err)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  }
}
</script>

<style>
#userManager {
  height: 100%;
  background-color: #fff;
}

.el-collapse {
  text-align: center;
}

#head-uploader .avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  width: 100px;
  height: 100px;
  border-radius: 100px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

#head-uploader .avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}

#head-uploader .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
}

#head-uploader .avatar {
  height: 100px;
  display: block;
}
</style>
