import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios'

import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
Vue.config.productionTip = false

Vue.use(mavonEditor)
Vue.prototype.$axios = axios
Vue.use(ElementUI)

axios.interceptors.request.use(
  config => {
    config.headers.Token = store.state.token
    return config
  }, error => {
    return Promise.reject(error)
  })

axios.interceptors.response.use(
  response => {
    return response
  }, err => {
    if (err.response.status === 401) {
      ElementUI.MessageBox.confirm('账号已过期, 是否跳转到登录页面?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        store.state.token = ''
        store.state.headImage = ''
        router.push('home')
      }).catch(() => { })
    } else if (err.response.status === 504) {
      ElementUI.Message.error('服务器无响应')
    } else {
      ElementUI.Message.error(err.response.data.msg)
    }
    return Promise.reject(err)
  })
router.beforeEach((to, from, next) => {
  if (to.name == null) {
    ElementUI.Message.error('路由不存在!')
  } else if (store.state.token !== '') {
    next()
  } else {
    if (to.name === 'Menu' || to.name === 'Home' || to.name === 'Article') {
      next()
    } else {
      ElementUI.Message({
        type: 'info',
        message: '用户未登录'
      })
      next({
        path: '/home'
      })
    }
  }
})
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
