<template>
  <div id="UserInfo">
    <div id="Info">
      <el-descriptions title="用户信息">
        <el-descriptions-item label="头像">
          <el-avatar :size="100" :src="form.headImg"></el-avatar>
        </el-descriptions-item>
        <el-descriptions-item label="账号">{{ form.id }}</el-descriptions-item>
        <el-descriptions-item label="昵称">{{ form.name }}</el-descriptions-item>
        <el-descriptions-item label="等级">{{ form.level }}</el-descriptions-item>
        <el-descriptions-item label="签名">{{ form.sign }}</el-descriptions-item>
      </el-descriptions>
    </div>
    <el-divider></el-divider>
    <div>
      <Card v-for="item in tableData" :key="item.id" :p_id="item.id" :p_title="item.title" :p_text="item.text"
        :p_uploadDate="item.uploadDate" :p_image="item.image" :p_content="item.html" :p_authorName="item.authorName"
        :p_authorId="item.authorId" :p_status="item.status" :p_collection="item.collection" :p_views="item.views">
      </Card>

      <el-pagination v-if="pageInfo.total" :page-sizes="[10, 20, 30, 50]"
        layout="total, sizes, prev, pager, next, jumper" :page-size.sync="queryForm.pageSize" :total="pageInfo.total"
        :current-page.sync="queryForm.pageNum" @size-change="queryCard" @current-change="queryCard">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import Card from '../article/Card.vue'
export default {
  name: 'UserInfo',
  data() {
    return {
      image: [],
      tableData: [],
      form: {},
      queryForm: {
        authorId: this.$store.state.authorId,
        pageNum: 1,
        pageSize: 10
      },
      pageInfo: {
        total: 0
      }
    }
  },
  created() {
    this.query()
    this.queryCard()
  },
  computed: {
    nullImage() {
      if (this.image.length === 0) return false
      else return true
    }
  },
  methods: {
    query() {
      this.$axios({
        method: 'post',
        url: 'api/user/anyone',
        data: {
          id: this.$store.state.authorId
        }
      }).then(res => {
        this.form = res.data.obj
        this.form.headImg = '/api/resources/' + this.form.headImg
      }).catch(err => {
        console.info(err)
      })
    },
    queryCard() {
      this.$axios({
        method: 'post',
        url: 'api/article/ofAuthor',
        data: this.queryForm
      }).then(res => {
        this.tableData = res.data.obj.list
        for (let i = 0; i < this.tableData.length; i++) {
          this.tableData[i].image = '/api/resources/' + this.tableData[i].image
        }
        this.pageInfo.total = res.data.obj.total
      }).catch(err => {
        console.info(err)
      })
    }
  },
  components: {
    Card
  }
}
</script>

<style scoped >
#UserInfo {
  height: auto;
  background-color: #ffffff;
}

#Info .el-descriptions {
  margin: 0 10px 0 10px;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}
</style>
