<template>
  <div id="MyInfo">
    <div id="my-info">
      <el-form :model="form" label-width="80px">
        <el-form-item label="头像">
          <div id="head-uploader">
            <el-upload class="avatar-uploader" action="" :http-request="elUploadImage" :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload" :show-file-list="false">
              <img v-if="form.headImg" :src="'/api/resources/' + form.headImg" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </div>
        </el-form-item>
        <el-form-item label="账号" prop="id">
          <el-input v-model="form.id" clearable disabled></el-input>
        </el-form-item>
        <el-form-item label="昵称" prop="name">
          <el-input v-model="form.name" clearable></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="phone">
          <el-input v-model="form.phone" clearable></el-input>
        </el-form-item>
        <el-form-item label="注册日期" prop="registrationDate">
          <el-input v-model="form.registrationDate" clearable disabled></el-input>
        </el-form-item>
        <el-form-item label="签名" prop="sign">
          <el-input v-model="form.sign" clearable maxlength="50" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="等级" prop="level">
          <el-input v-model="form.level" disabled></el-input>
        </el-form-item>
        <el-form-item label="经验" prop="experience">
          <el-input v-model="form.experience" disabled></el-input>
        </el-form-item>
      </el-form>
      <div style="text-align:center ;">
        <el-button type="primary" @click="submitForm">提交</el-button>
        <el-button @click="back">返回</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MyInfo',
  data() {
    return {
      queryForm: {
        pageNum: 1,
        pageSize: 5
      },
      pageInfo: {
        total: 0
      },
      show: false,
      form: {},
      disabled: true
    }
  },
  created() {
    this.query()
  },
  methods: {
    back() {
      this.$router.go(-1)
    },
    submitForm() {
      this.$axios({
        method: 'post',
        url: 'api/user/update',
        data: this.form
      }).then(res => {
        this.$message({
          type: 'success',
          message: res.data.msg
        })
        this.show = false
        this.$store.commit('headImage', '/api/resources/' + this.form.headImg)
        this.query()
        this.$forceUpdate()
      }).catch(err => {
        console.info(err)
      })
    },
    query() {
      this.$axios({
        method: 'post',
        url: 'api/user/myInfo',
        data: this.queryForm
      }).then(res => {
        this.form = res.data.obj
      }).catch(err => {
        console.info(err)
      })
    },
    handleAvatarSuccess(res, file) {
      this.form.image = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!')
      }
      return isLt2M
    },
    elUploadImage($file) {
      const formdata = new FormData()
      formdata.append('image', $file.file)
      this.$axios({
        method: 'post',
        url: 'api/article/uploadImage',
        data: formdata,
        headers: { 'Content-Type': 'multipart/form-data' }
      }).then(res => {
        this.$forceUpdate()
        this.form.headImg = res.data.obj
      }).catch(err => {
        console.info(err)
      })
    }
  }
}
</script>

<style>
#MyInfo {
  height: auto;
  background-color: #efefef;
  padding-top: 10px;

}

#my-info {
  width: 340px;
  margin: auto;
}

.el-collapse {
  text-align: center;
}

#head-uploader .avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  width: 100px;
  height: 100px;
  border-radius: 100px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

#head-uploader .avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}

#head-uploader .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
}

#head-uploader .avatar {
  height: 100px;
  display: block;
}
</style>
